Rate.ChartExchange = (function(){
  function ChartExchange(container) {
  	this.container = container;
  	this.chartPresented = this.container.find(".highcharts-container");
  	this.loading = this.container.find(".loading");
  	this.placeholder = this.container.find(".placeholder");
  }
 
  ChartExchange.fn = ChartExchange.prototype;

  ChartExchange.fn.init = function() {
    this.load("USD", "Dlar");
  };

  ChartExchange.fn.load = function(code, name) {
  	this.showLoading();
  	this.data = new Rate.ChartExchange.Data(code, name);

  	var response = this.data.request();
  	response.done(function(settings){
  		this.render(settings);
  	}.bind(this));
    
  };

  ChartExchange.fn.showLoading = function(){
  	this.chartPresented.hide();
  	this.loading.show();
  }

  ChartExchange.fn.render = function(data) {
  	this.loading.hide();
    this.placeholder.highcharts(data);
    this.chartPresented = this.container.find(".highcharts-container")
  };
 
  return ChartExchange;
})();